@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');



* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    /* --color-bg: white;
    --color-bg-variant: #ffe1cc;
    --color-primary: #ff914d;
    --color-primary-variant: rgba(255, 145, 77, 0.4);
    --color-white: #333; 
    --color-light: rgba(128, 128, 128, 0.6);
    --color-imgbg: #ffd8b5; */

    /* --color-bg: #1b1b38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77,181,255,0.4);
    --color-white: #fff;
    --color-light: rgba(241, 234, 234, 0.6);
    --color-imgbg: #49a793; */

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body {
    font-family: 'Roboto', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    /* background-image: url(../src/assets/bgtexture.png); */
}

/* ============ GENERAL STYLES ============*/
.container {
    width: var(--container-width-lg);    
    margin: 0 auto; 
}

h1,h2,h3,h4,h5 {
    font-weight: 500;
}


h1{
    /* 2.5 times of root element */
    font-size: 2.5rem; 
}

section {
    padding-top: 2rem;
    margin-top: 8rem;
    height: auto;
}

section > h2,
section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover{
    color: var(--color-light);
}
.btn {
    width:max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}
.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}
img {
    display: block;
    /* scale to the whole container */
    width: 100%;
    /*  */
    object-fit: cover;
}

/* ============ MEDIA QUERIES ============*/
@media screen and (max-width: 1024px){
    .container{
        width:var(--container-width-md);
    }
    section {
        margin-top: 10rem;
    }
}
@media screen and (max-width: 600px){
    .container{
        width:var(--container-width-sm);
    }
    section {
        margin-top: 10rem;
    }
    section > h2{
        margin-bottom: 2rem;
    }
}